import { Component } from '@angular/core';
import {FormGroup,FormControl,Validators} from '@angular/forms';
import {ConexionService} from '../../services/conexion.service';
@Component({
  selector: 'app-anexo5',
  templateUrl: './anexo5.component.html',
  styleUrls: ['./anexo5.component.css']
})

export class Anexo5Component{
anexo5:FormGroup;

dia=new Date().getDay();
mes=new Date().getMonth();
año=new Date().getFullYear();
fecha=`${this.dia}/${this.mes}/${this.año}`;

  constructor(private conexion:ConexionService) {
      this.anexo5=new FormGroup({
        'datos_generales':new FormGroup({
          'nombre_tutor':new FormControl('', [Validators.required, Validators.minLength(3)]),
          'fecha':new FormControl(this.fecha),
        }),
        'carrera':new FormGroup({
          'plan_estudios':new FormControl('', [Validators.required, Validators.minLength(3)]),
          'semestre':new FormControl('')
        }),

        'tipo_tutoria':new FormGroup({
          'grupal':new FormGroup({
            'numero_tutorados':new FormControl('')
          }),
          'individual':new FormGroup({
            'nombre_tutorado':new FormControl(''),
            'numero_control':new FormControl('')
          }),
        }),
        'problematica':new FormControl(''),
        'objetivos':new FormControl(''),
        'acciones':new FormControl(''),
        'calendarizacion':new FormGroup({
          'actividad_1':new FormGroup({
            'actividad':new FormControl(''),
            'fecha_1':new FormControl(''),
            'fecha_2':new FormControl(''),
            'fecha_3':new FormControl('')
          }),
          'actividad_2':new FormGroup({
            'actividad':new FormControl(''),
            'fecha_1':new FormControl(''),
            'fecha_2':new FormControl(''),
            'fecha_3':new FormControl('')
          }),
          'actividad_3':new FormGroup({
            'actividad':new FormControl(''),
            'fecha_1':new FormControl(''),
            'fecha_2':new FormControl(''),
            'fecha_3':new FormControl('')
          }),
        }),
      })


  }

  guardarCambios(){
    const fields = this.anexo5.value;
    var tutorship;

    if (fields.tipo_tutoria.grupal.numero_tutorados === '') {
      tutorship = fields.tipo_tutoria.individual.nombre_tutorado + ' ' + fields.tipo_tutoria.individual.numero_control;
    } else {
      tutorship = fields.tipo_tutoria.grupal.numero_tutorados;
    }

    var activity = fields.calendarizacion.actividad_1.actividad;
    activity += ' ' + fields.calendarizacion.actividad_1.fecha_1;
    activity += ' ' + fields.calendarizacion.actividad_1.fecha_2;
    activity += ' ' + fields.calendarizacion.actividad_1.fecha_2;

    const form = {
      name: fields.datos_generales.nombre_tutor,
      date: fields.datos_generales.fecha,
      plan: fields.carrera.plan_estudios,
      semester: fields.carrera.semestre,
      tutorship,
      problem: fields.problematica,
      objective: fields.objetivos,
      action: fields.acciones,
      activity
    };

    this.conexion.eviarDatos(form);
  }
}
